import { Component } from '@angular/core';
import { IonicPage, AlertController, NavController, NavParams, LoadingController, ToastController } from 'ionic-angular';
import { UserProvider } from '../../providers/user/user';
@IonicPage({
  name: 'page-transaction-summary',
  segment: 'transaction-summary'
})

@Component({
  selector: 'page-transaction-summary',
  templateUrl: 'transaction-summary.html'
})

export class TransactionSummaryPage {
  propertyName: any;
  amount: any;
  constructor(public navCtrl: NavController, public navparams: NavParams, public user: UserProvider, public alertCtrl: AlertController, public loadingCtrl: LoadingController, public toastCtrl: ToastController) {

    this.propertyName = this.navparams.get('propertyname');
    this.amount = this.navparams.get('amount');
    console.log(this.amount);
  }


}



